input {
    font-family: 'Quicksand', sans-serif;
    font-weight: bold;
    padding: 20px;
    font-size: 14px;
    background-color: var(--color-accent);
    color: var(--color-bg);
    border: 2px solid var(--color-accent);
    border-radius: 4px;
    box-shadow: rgba(100, 100, 111, 0.5) 0px 7px 29px 0px;
}

input[type=text]:focus {
    border: 2px solid var(--color-fg);
    outline: none;
}

.taskInput {
    position: fixed;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
}

.inputProps {
    display: flex;
}

.priority, .tag {
    padding: 5px 10px;
    margin: 5px;
    border-radius: 4px;
    color: var(--color-bg);
}

.priority {
    background-color: var(--color-priority1);
}

.tag{ 
    background-color: var(--color-tag);
}